import React from 'react';
//import names from './Names.json'; // Ajusta la ruta segn tu estructura

const names  = {
  "PEF05":"Peso por edad en nias desde el nacimiento a 5 aos",
  "PEM05": "Peso por edad en nios desde el nacimiento a 5 aos",
  "TEF05": "Longitud/estatura por edad en nias desde el nacimientos a 5 aos",
  "TEM05": "Longitud/estatura por edad en nios desde el nacimientos a 5 aos",
  "PTF02": "Peso por longitud en nias desde el nacimiento a 2 aos, de 45 cm a 110 cm",
  "PTM02": "Peso por longitud en nios desde el nacimiento a 2 aos, de 45 cm a 110 cm",
  "PTF25": "Peso por estatura en nias de 2 aos a 5 aos, de 65 cm a 120 cm",
  "PTM25": "Peso por estatura en nios de 2 aos a 5 aos, de 65 cm a 120 cm",
  "PEF510": "Peso por edad en nias de 5 aos 1 mes a 10 aos",
  "PEM510": "Peso por edad en nias de 5 aos 1 mes a 10 aos",
  "TEF519": "Estatura por edad en nias de 5 aos 1 mes a 19 aos",
  "TEM519": "Estatura por edad en nios de 5 aos 1 mes a 19 aos",
  "IMCF519": "IMC por edad en nias y adolescentes de 5 aos 1 mes a 19 aos",
  "IMCM519": "IMC por edad en nios y adolescentes de 5 aos 1 mes a 19 aos"
}

const Table = ({ title, data }) => {
  if (!data || Object.keys(data).length === 0) return <p>No data available</p>;

  // Funcin para redondear nmeros
  const formatNumber = (value) => {
    return typeof value === 'number' ? value.toFixed(2) : value;
  };

  // Obtn los encabezados de la primera entrada de data
  const headers = Object.keys(data[Object.keys(data)[0]]);
  
  // Crea las filas de la tabla
  const rows = Object.keys(data).map(rowKey => (
    <tr key={rowKey}>
      {Object.values(data[rowKey]).map((value, index) => (
        <td key={index}>{formatNumber(value)}</td>
      ))}
    </tr>
  ));

  // Obtn la descripcin del ttulo usando el archivo JSON
  const tableTitle = names[title] || 'Ttulo no disponible';
  //console.log('Table Title:', tableTitle);

  // Determina la clase para el ttulo y la tabla
  const titleClass = tableTitle.toLowerCase().includes('nias') ? 'ninas' :
                     tableTitle.toLowerCase().includes('nios') ? 'ninos' : '';
  //console.log('Title Class:', titleClass);

  return (
    <div>
      <h2 className={titleClass}>{tableTitle}</h2>
      <table className={titleClass}>
        <thead>
          <tr>
            {headers.map((header, index) => (
              <th key={index}>{header}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {rows}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
